import React from 'react'
import { Modal, Form, Row, Col, Input, DatePicker, Divider } from 'antd'

export default function AddReportMoreData({open, setOpen, data, setData}) {
    let [form] = Form.useForm()

    const onFinish = (values) => {
        console.log('Success:', values);
      };
    
      const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
      };

    return (
        <Modal
            visible={open}
            okText="បញ្ចូល"
            cancelText="បញ្ឈប់"
            onOk={() => onFinish()}
            onCancel={() => setOpen(false)}
            title="បញ្ចូលបន្ថែម"
        >
            <Form
                name="addReportMoreData"
                form={form}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Divider>១.ព័ត៌មានផ្ទាល់ខ្លួនអ្នកជំងឺ</Divider>
                    <Col
                        xs={{span:11, offset:0}}
                    >
                        <Form.Item
                            name="dateOfBirth"
                        >
                            <DatePicker placeholder="ថ្ងៃខែឆ្នាំកំណើត" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:11, offset:2}}
                    >
                        <Form.Item
                            name="social"
                        >
                            <Input placeholder="បណ្តាញសង្គម"/>
                        </Form.Item>
                    </Col>
                    <Divider>៤.ប្រវត្តិដំណើរអ្នកជំងឺ</Divider>
                    <Col
                        xs={{span:11, offset:0}}
                    >
                        <Form.Item
                            name="dateOfArrived"
                        >
                            <DatePicker placeholder="ថ្ងៃមកមកដល់កម្ពុជា" style={{width: "100%"}} />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:11, offset:2}}
                    >
                        <Form.Item
                            name="comefrom"
                        >
                            <Input placeholder="មកពីប្រទេស"/>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:11, offset:0}}
                    >
                        <Form.Item
                            name="target"
                        >
                            <Input placeholder="គោលបំណងមកកម្ពុជា"/>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:11, offset:2}}
                    >
                        <Form.Item
                            name="dateOfGoto"
                        >
                            <Input placeholder="ថ្ងៃចាកចេញពីកម្ពុជា"/>
                        </Form.Item>
                    </Col>
                    <Col
                        xs={{span:24, offset:0}}
                    >
                        <Form.Item
                            name="goto"
                        >
                            <Input placeholder="ទៅកាន់ប្រទេស"/>
                        </Form.Item>
                    </Col>
                    <Divider>៥.ជំងឺប្រចាំកាយ</Divider>
                    <Col
                        xs={{span:24, offset:0}}
                    >
                        <Form.Item
                            name="other"
                        >
                            <Input placeholder="ផ្សេងៗ"/>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
