import { Space, Popconfirm } from 'antd'
import {
    EyeOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';
import moment from 'moment';

export const sampleCol = ({handleSampleTestDelete,limit,page}) => {
    let no = ((page-1) * limit)
    var array = [
        {
            title: 'ល.រ',
            dataIndex: 'id',
            key: 'id',
            width: 30,
            render: (text, record, i) => (
                <span>
                    {no += 1}
                </span>
            ),
        },
        {
            title: 'អត្តសញ្ញាណប័ណ្ណ',
            dataIndex: 'idCard',
            key: 'idCard',
            width: 100,
        },
        {
            title: 'លេខសម្គល់អ្នកជំងឺ',
            dataIndex: 'patientId',
            key: 'patientId',
            width: 100,
        },
        {
            title: 'ឈ្មោះ',
            dataIndex: 'name',
            key: 'name',
            width: 80,
            render: (text, record) => (
                <span>
                    {record?.lastName+" "+record?.firstName}
                    {/* {console.log(record)} */}
                </span>
            ),
        },
        {
            title: 'កាលបរិច្ឆេទវិជ្ជមាន',
            dataIndex: 'confirmedAt',
            key: 'confirmedAt',
            width: 70,
            render: (text, record) => (
                <span>
                    {record?.currentState?.confirmedAt === null ? "" : moment.parseZone(record?.currentState?.confirmedAt).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                </span>
            ),
        },
        {
            title: 'កាលបរិច្ឆេទស្លាប់',
            dataIndex: 'deathAt',
            key: 'deathAt',
            width: 70,
            render: (text, record) => (
                <span>
                    {record?.currentState?.deathAt === null ? "" : moment.parseZone(record?.currentState?.deathAt).format("ថ្ងៃទីDD ខែMM ឆ្នាំYYYY")}
                </span>
            ),
        },
        {
            title: 'ចំនួនយកសំណាក',
            dataIndex: 'times',
            key: 'times',
            width: 60,
            render: (text, record) => (
                <span>
                    {record?.sampleTest?.length} លើក
                </span>
            ),
        },
        // {
        //     title: 'ទីតាំង',
        //     dataIndex: 'location',
        //     key: 'location',
        //     width: 50,
        // },
        {
            title: 'ស្ថានភាព',
            dataIndex: 'result',
            key: 'result',
            width: 60,
            render:(text,record)=>(
                <Space size="middle">
                    { record?.currentState?.confirm ? "វិជ្ជមាន":"អវិជ្ជមាន" }
                </Space>
            )
        },
        // {
        //     title: 'អាការ',
        //     dataIndex: 'symptom',
        //     key: 'symptom',
        //     width: 50,
        // },
        {
            title: 'ផ្សេងៗ',
            dataIndex: 'other',
            key: 'other',
            width: 100,
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 30,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                    <Popconfirm
                        title="តើអ្នកពិតចង់លុបមែនឬទេ?"
                        onConfirm={() => { handleSampleTestDelete(record.id) }}
                        okText="ចង់"
                        cancelText="មិនចង់"
                    >
                        <span className="link" style={{ color: "red" }}><DeleteOutlined /></span>
                    </Popconfirm>

                </Space>
            ),
        }
    ]

    return array
}